/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import notificationsData from './data-bb-notifications-http';

const notificationsDataModuleKey = 'data-bb-notifications-http-ng';

export const notificationsDataKey = 'data-bb-notifications-http-ng:notificationsData';

export default ng
  .module(notificationsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(notificationsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       notificationsData(config),
      ],
    };
  }])
  .name;



